.main-ncs-header-div{
    background-color: #fff;
    padding: 12px 0px;
    position: sticky;
    top: 0px;
    z-index: 10;
    display: block;
    box-shadow: 10px 2px 26px -6px black;
}

.logo-div {
  h1 {
    color: #000;
    font-family: Urbanist;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 22.393px;
    margin: 0px;
  }
  display: flex;
  align-items: center;
  gap: 5px;

  @media (max-width: 900px) {
  h1 {
    font-size: 13px;
  }
}
}
.nav-link{
  color: #000;
    font-family: Urbanist;
font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: 22.393px; /* 139.957% */
text-decoration: none !important;
}
.active-link{
    color: #000;
    font-weight: bold;
}
.arrow-icon {
  display: flex;
    transition: transform 0.3s ease;
}
/* Rotate the arrow when dropdown is expanded */
.show .arrow-icon {
    transform: rotate(180deg);
}
.nav-item{
  cursor: pointer;
}

.main-epfo-header{
    background: #FFF;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content:end;
    width: 100%;
    box-shadow: 18px -6px 6px 6px rgb(163, 163, 163);
}
.user-info {
  margin-right: 8px;
  text-align: right;
}

.name {
  font-size: 14px;
  font-weight: 500;
  color: #2c2c2c;
}

.role {
  font-size: 12px;
  color: #9e9e9e;
}

.avatar {
  width: 36px;
  height: 36px;
  background-color: rgba(126, 87, 194, 0.1); /* light purple */
  color: #673ab7; /* purple text */
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  margin-left: 8px;
  overflow: hidden; /* keeps image inside circle */
  text-transform: uppercase;
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;  /* ensures proper fill without distortion */
  object-position: center; /* centers the image */
  border-radius: 50%;
  display: block;
}

.btn-check:checked+.btn, .btn.active, .btn.show, .btn:first-child:active, :not(.btn-check)+.btn:active {
    color: var(--bs-btn-active-color);
    background-color: var(--bs-btn-active-bg);
    border-color:#ffffff00;
}
